<template>
  <div id="plan">
    <div class="text-left">
      <!-- 筛选区 -->
      <el-row class="serach-box">
        <el-col :span="4" v-for="item in headerItems" :key="item">
          <el-col :span="7">{{item.label}}</el-col>
          <el-col :span="16">
            <el-input v-model="storeQuery.customer"
            ><el-button
            @click="query"
              slot="append"
              icon="el-icon-search"
            ></el-button></el-input
        >
          </el-col>
        </el-col>
        <el-col :span="4">
          <el-col :span="7">状态</el-col>
          <el-col :span="16">
            <el-select @change="query" v-model="storeQuery.bookingListState">
            <el-option
              v-for="item in statusTypeList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
          </el-col>
        </el-col>
        <el-col :span="4">
          <el-date-picker
            v-model="dataValue"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <!-- 操作区 -->
      <el-row class="serach-box">
        <el-col :span="4">
          <el-col :span="7">批量更新</el-col>
          <el-col :span="16">
            <el-select v-model="selectStatus">
            <el-option
              v-for="item in statusChangeList"
              :key="item.index"
              :value="item"
            ></el-option>
          </el-select>
          </el-col>
        </el-col>
        <el-col class="btn-area" :span="8">
        <el-col :span="6"><el-button @click="updateStatus" type="info">更新状态</el-button></el-col>
         <el-col :span="6"><el-button type="info">导出</el-button></el-col>
        <el-col :span="6"
          ><el-button @click="newOrderStore" type="info">新增</el-button></el-col
        >
        <el-col :span="6"><el-button @click="remove" type="danger">删除</el-button></el-col>
        </el-col>
      </el-row>
      <!-- 表格区 -->
      <el-row>
        <el-table border @select="rowclick" :data="tableData">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column
            type="index"
            width="50"
            label="序号"
            fixed
          ></el-table-column>
          <el-table-column
            :fixed="item.isFix || false"
            :label="item.label"
            :prop="item.prop"
            :width="item.width"
            v-for="item in tableList"
            :key="item.index"
          >
          </el-table-column>
          <el-table-column width="140" label="操作"
            ><template slot-scope="scope"><el-button @click="edit(scope)" size="mini">编辑</el-button></template></el-table-column
          >
        </el-table>
      </el-row>
    </div>
  </div>
</template>

<script>
import { GetAllBookingList,UpdateStateBookingList,DeleteBookingList} from "network/orderStore.js";
import { GetCommonFormState } from "network/common.js"

import {UpdateState} from "service/Business/orderStore.js"

export default {
  data() {
    return {
      dataValue: null,
      statusTypeList: [
      ],
      statusChangeList:[],
      selectStatus:null,
      statusType: null,
      selectData:[],
      headerItems:[{
        label:"客户",
        prop:"customer"
      },{
        label:"港口",
        prop:"shippingAddress",
      },{
        label:"订单号",
        prop:"orderNo",
      },{
        label:"货号",
        prop:"itemNo",
      }],
      tableList: [
        { label: "客户", prop: "customer", width: "100", isFix: "left" },
        { label: "港口", prop: "shippingAddress", width: "100", isFix: "left" },
        { label: "进仓日期", prop: "warehousingDate", isFix: "left" },
        { label: "订单号", prop: "orderNo", isFix: "left" },
        { label: "货号", prop: "itemNo", isFix: "left" },
        { label: "出货数", prop: "totalItemNo", width: "100", isFix: "left" },
        { label: "箱数", prop: "totalBoxNo", width: "100", isFix: "left" },
        {
          label: "状态",
          prop: "bookingListState",
          width: "100",
          isFix: "left",
        },
      ],
      tableData: [],
      storeQuery: {
        pageNumber: 0,
        pageSize: 10,
        customer: "",
        shippingAddress: "",
        beginTime: "2020-12-23",
        endTime: "2021-12-23",
        orderNo: "",
        itemNo: "",
        bookingListState: "修改中",
      },
    };
  },
  watch:{
    dataValue(val){
      this.storeQuery.beginTime = val[0];
      this.storeQuery.endTime = val[1];
      this.query();
    }
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      this.getOrderStore(this.storeQuery);
      this.getStatusList();
    },
    //获得表状态列表
    getStatusList(){
      GetCommonFormState().then(res=>{
        if(res.success){
          this.statusTypeList = res.result;
          this.statusChangeList = res.result;
        }
      })
    },
    //新增订舱单
    newOrderStore() {
      this.$router.push({
        path:"/newOrderStore",
      });
    },
    //获得订舱单列表
    getOrderStore(data) {
      GetAllBookingList(data).then((res) => {
        if (res.success) {
          this.tableData = res.result.items;
          this.$message.success("获得订舱单列表成功");
        } else {
          this.$message.error("获得订舱单列表失败");
        }
      });
    },
    //点击选择框
    rowclick(selection, row){
      this.selectData = selection;
    },
    //点击查询
    query(){
      this.getOrderStore(this.storeQuery);
    },
    //批量更新
    updateStatus(){
      var selectData = this.selectData;
      if(selectData.length ==0){
        this.$message.error('请先选择项!');
      }
      else{
        let data = new UpdateState(selectData,this.selectStatus);
        UpdateStateBookingList(data).then(res=>{
          this.$message.success('更新状态成功');
        })
      }
    },
    //编辑
    edit(scope){
      this.$router.push({
        path:"/newOrderStore",
        query:{iid:scope.row.id}
      });
    },
    //批量删除
    remove(){
      var data = this.selectData;
      if(data.length==0){
        this.$message.error('请先选择项！');
      }
      else{
        let arr = new Array(data.length);
        data.forEach((item,index) => {
          arr[index] = item.id;
        });
        DeleteBookingList(arr).then(res=>{
          if(res.success){
            this.$message.success("删除成功")
          }
        })
      }
    }
  },
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
.serach-box {
  text-align: left;
  line-height: 40px;
}
</style>